<template>
  <el-card :body-style="{textAlign:'left', overflowY:'auto',height:'auto'}">
    <div slot="header">
      <span></span>
      <el-button style="float: right;padding: 3px 0" type="text">x</el-button>
    </div>
    <el-form size="mini">
      <div v-for="evidence in data.EvidenceExtract.evidences" :key="evidence">
        <el-form-item v-if="evidence.type==='file'">
          <el-col :span="12">
            {{ evidence.name }}
          </el-col>
          <el-col :span="12">
            <el-upload
                :before-remove="beforeRemove"
                :file-list="evidence.value"
                :limit="1"
                :on-exceed="handleExceed"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                action="https://jsonplaceholder.typicode.com/posts/"
                class="upload-demo"
                multiple>
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
            </el-upload>
          </el-col>
        </el-form-item>
        <el-form-item v-if="evidence.type==='comment'">
          <el-col :span="12">
            {{ evidence.name }}
          </el-col>
          <el-col :span="12">
            <el-input v-model="evidence.value" :rows="2"
                      placeholder="" type="textarea"></el-input>
          </el-col>
        </el-form-item>
      </div>
    </el-form>
    <div id="confirm_buttons" style="text-align: center">
      <el-button round size="mini" type="success" @click="open"></el-button>
      <el-button round size="mini" type="danger"></el-button>
    </div>
  </el-card>
</template>

<script>
export default {
  name: "EvidenceExtractPanel",
  data() {
    return {
      visible: false,
      data: {
        EvidenceExtract: {
          name: "",
          evidences: {
            Source: {
              name: "",
              type: "file",
              value: ""
            },
            Type: {
              name: "",
              type: "comment",
              value: ""
            },
          }
        },
      }
    }
  },
  methods: {
    open() {
      this.$alert('', '', {
        confirmButtonText: '',
        callback: () => {
          this.$message({
            type: 'success',
            message: `!`
          });
        }
      });
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
    },
    beforeRemove(file) {
      return this.$confirm(` ${file.name}`);
    }
  }
}
</script>

